import React from 'react';
import styles from './Article.module.css';
import PDF from '../PDF';
import TextFile from '../TextFile';

const page = () => {
  return (
    <div className={styles.article}>
        <h2 className={styles.subtitle} >Compétition entre groupes</h2>
        <TextFile filePath={`${process.env.PUBLIC_URL}/data/text/tsp.anonymous.log`} />
        <div className= {`${styles.paragraph_50} ${styles.padding1em}`} >
            <p>A la fin du module, 
                nous avons participé à une compétition en classe où chaque groupe de deux étudiants 
                devait résoudre le problème du voyageur de commerce (TSP) en utilisant différentes approches algorithmiques. 
            </p>
            <p>
                Les performances des projets ont été évaluées sur 100 exécutions de 60 secondes chacune, 
                couvrant 10 projets et 4 problèmes différents.
            </p>
            <p>
                Notre projet, nommé BakiTKO, a obtenu les meilleurs résultats de la compétition. 
                Voici un aperçu des performances de notre projet par rapport aux autres, 
                avec chaque nom représentant un problème et le nombre de points de l'instance, puis la distance minimale trouvée :
            </p>
            <ul className={styles.list}>
                <li>bier127 : 118304.14112602551</li>
                <li>gr666 : 3156.8917857904808</li>
                <li>pr8192 : 16382.0</li>
                <li>surprise903 : 4363.962594210435</li>
            </ul>
            <h3 className={styles.subtitle}>Interprétation générale des résultats :</h3>
            <p>Les résultats montrent que notre projet BakiTKO a obtenu des distances minimales très compétitives pour chaque problème, 
                démontrant l’efficacité de notre approche algorithmique. 
            </p>
            <p>Vous pouvez voir un compte rendu plus détaillé de ce projet ci-dessous.</p>

        </div>
        <div className={styles.paragraph_100}>
            <h3 className={styles.subtitle}>Origine du Nom du Projet</h3>
            <p> Notre projet, nommé BakiTKO, tire son nom du combat entre Baki (Baïssangour Chamsoudinov) et Doumbé qui a eu lieu pendant la même période que notre projet. </p>
            <p>Ce choix de nom est une référence à la détermination démontrées lors de ce combat. </p>
            <p>Le terme TKO (Technical Knockout) symbolise notre objectif de surpasser les autres projets de manière décisive, en trouvant des solutions optimales et efficaces pour le problème du voyageur de commerce (TSP).</p>
        </div>
        <h2 className={styles.subtitle} >Compte rendu</h2>
        <PDF filePath={`${process.env.PUBLIC_URL}/data/pdf/MH_Rapport.pdf`} />
    </div>
  );
}

export default page;